/* Contact.css */

.form {
    margin: 20px auto;
    max-width: 600px;
  }
  
  .contact-form {
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 255, 255, 0.5);
  }
  
  .contact-form h2 {
    color: #00ffff;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .contact-form label {
    color: #00ffff;
    display: block;
    margin-bottom: 10px;
  }
  
  .contact-form input[type='text'],
  .contact-form input[type='email'],
  .contact-form input[type='tel'],
  .contact-form textarea {
    width: 100%;
    padding: 15px;
    margin-bottom: 20px;
    border: none;
    border-radius: 5px;
    background-color: #5d5d5d36;
    color: #fff;
  }
  
  .contact-form .services-checkboxes {
    color: #00ffff;
    margin-bottom: 20px;
  }
  
  .contact-form .service-checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .contact-form .service-checkbox input[type='checkbox'] {
    margin-right: 10px;
  }
  
  .contact-form .service-checkbox label {
    color: #00ffff;
    flex: 1;
  }
  
  .contact-form textarea {
    resize: vertical;
  }
  
  .contact-form .btn {
    width: 100%;
    padding: 15px;
    background-color: #00ffff;
    color: #000;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-form .btn:hover {
    background-color: #00ccff;
  }
  
  .success-message,
  .error-message {
    text-align: center;
    margin-bottom: 20px;
    color: #00ffff;
  }
  