/* AboutSection.css */

.about-section {
    display: flex;
    flex-direction: row;
    padding: 20px;
    justify-content: center;
    align-items: center;
    border:  3px solid rgba(47, 255, 252, 0.484);
    height: 700px;
    margin-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .about-content {
    margin-bottom: 20px;
  }
  
  .about-content h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: rgb(47, 255, 252);
    text-align: center;
  }
  
  .about-content p {
    font-size: 16px;
    color: rgba(47, 255, 252, 0.81);
    line-height: 1.6;
    text-align: justify;
    padding: 20px;
  }
  
  .about-image img {
    height: 200px;
    width: 400px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }

@media(max-width:900px){
    .about-section {
        flex-direction: column;
        padding: 15px;
      }

      .about-image img {
        height: 200px;
        width: 320px;
        border-radius: 8px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      }
      
}
  