.screen{
    width: 700px;
    height: 350px;
    border: 10px solid rgb(63, 63, 63);
    box-shadow: 3px 4px 22px rgba(47, 255, 252, 0.481);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.tv-main-screen{
    width: 100%;
    height: 100%;
}

.tv-navbar{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tv-navbar .menu{
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    width: 100%;
}

.tv-navbar .menu a{
    color: rgba(255, 255, 255, 0.529);
    background: rgba(9, 218, 255, 0.456);
    border: 1px solid rgba(255, 255, 255, 0.279);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    margin-bottom: 10px;
}

.tv-navbar .menu a:hover{
    color: rgb(255, 255, 255);
    box-shadow: 3px 4px 22px rgba(47, 255, 252, 0.708);
    background: rgb(9, 218, 255);
}


@media(max-width:900px){
    .screen{
        width: 320px;
        height: 200px;
        border: 6px solid rgb(63, 63, 63);
    }
}