@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body{
  background: #1e2127;
}

html,body{
  width: 100%;
  overflow-x: hidden;
}

.btn{
  padding: 10px 20px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  background: linear-gradient(270deg,transparent,#03e9f4);
  border: 1px solid rgb(47, 255, 252);
  color: rgb(255, 255, 255);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.btn-1{
  padding: 10px 20px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  background: linear-gradient(270deg,transparent,#03e9f4);
  border: 1px solid rgb(47, 255, 252);
  color: rgb(255, 255, 255);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.btn-submit{
  padding: 10px 20px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  background: linear-gradient(270deg,transparent,#03e9f4);
  border: 1px solid rgb(47, 255, 252);
  color: rgb(255, 255, 255);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media(max-width:900px){
  .btn-1{
    display: flex;
  }
}
@media(min-width:900px){
  .btn{
    display: none;
  }
}

.btn:hover{
  color: rgb(0, 0, 0);
  box-shadow: 3px 4px 22px rgba(47, 255, 252, 0.481);
  background: rgb(47, 255, 252);
}

.para{
  color:rgba(47, 255, 252, 0.455);
  margin-bottom: 30px;
  letter-spacing: 4px;
  padding: 5px;
  font-size: 0.7rem;
}

.para {
  animation: glow 1s ease-in-out infinite alternate;
}

@keyframes glow {
  0% {
    text-shadow: 0 0 10px #00ffff, 0 0 20px #00ffff, 0 0 30px #00ffff;
  }
  100% {
    text-shadow: 0 0 20px #00ffff, 0 0 30px #00ffff, 0 0 40px #00ffff;
  }
}

.glow{
  animation: glow1 1s ease-in-out infinite alternate;
}

@keyframes glow1 {
  0% {
    box-shadow: 0 0 5px #00ffff, 0 0 10px #00ffff, 0 0 20px #00ffff;
  }
  100% {
    box-shadow: 0 0 10px #00ffff, 0 0 15px #00ffff, 0 0 30px #00ffff;
  }
}