/* ContactScreen.css */

.contact-screen {
    width: 100%;
    height: 290px;
    margin: auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow-y: scroll; /* Enable scrolling if content exceeds dimensions */
    overflow-x: hidden;
  }
  
  .contact-screen h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px; /* Increase font size for smaller screens */
  }
  
  .contact-screen label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px; /* Increase font size for smaller screens */
  }
  
  .contact-screen input,
  .contact-screen textarea {
    width: calc(100% - 20px); /* Adjust width to account for padding */
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px; /* Increase font size for smaller screens */
  }
  
  .contact-screen button {
    width: calc(100% - 20px); /* Adjust width to account for padding */
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 18px; /* Increase font size for smaller screens */
  }
  
  .contact-screen button:hover {
    background-color: #0056b3;
  }
  
  .success-message,
  .error-message {
    text-align: center;
    margin-bottom: 10px;
    font-size: 16px; /* Increase font size for smaller screens */
  }
  
  .success-message {
    color: green;
  }
  
  .error-message {
    color: red;
  }
  
  /* Responsive styles */
  @media screen and (max-width: 700px) {
    .contact-screen {
      display: none;
    }
  }
  