/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    box-shadow: 0 0 20px rgba(15, 234, 223, 0.3);
    position: fixed;
    width: 100%;
    z-index: 10;
  }
  
  .brand {
    font-size: 1.5rem;
    color: rgb(47, 255, 252);
    text-decoration: none;
  }
  
  .navbar .menu {
    display: flex;
  }
  
  .navbar .menu a {
    color: rgb(47, 255, 252);
    text-decoration: none;
    margin: 0 15px;
    font-size: 1rem;
  }

  .navbar  .menu a:hover{
    text-decoration: underline;
    text-underline-offset: 10px;
    text-decoration-thickness: 2px;
  }
  
  .book-meeting {
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    background: transparent;
    border: 1px solid rgb(47, 255, 252);
    color: rgb(47, 255, 252);
  }

  .book-meeting:hover {
    color: rgb(0, 0, 0);
    box-shadow: 3px 4px 12px rgba(47, 255, 252, 0.481);
    background: rgb(47, 255, 252);
  }
  .book-meeting1{
    padding: 10px 20px;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    display: none;
    background: transparent;
    border: 1px solid rgb(47, 255, 252);
    color: rgb(47, 255, 252);
  }
  
  .mobile-menu-icon {
    display: none;
    flex-direction: column;
    cursor: pointer;
    transition: transform 0.25s ease-in-out;
  }
  
  .mobile-menu-icon.open {
    transform: rotate(90deg);
  }
  
  .bar {
    width: 25px;
    height: 3px;
    background-color:rgb(47, 255, 252);
    margin: 6px 0;
  }
  
  /* Media Query for Mobile View */
  @media screen and (max-width: 768px) {
    .navbar .menu {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 70px;
      left: 0;
      right: 0;
      z-index: 1;
      padding: 20px;
      background: #1e2127a6;
      box-shadow: 3px 3px 10px rgb(47, 255, 252);
    }
  
    .menu.mobile-menu-open {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  
    .mobile-menu-icon {
      display: flex;
    }

    .navbar .menu a{
      padding: 5px;
    }

    .book-meeting{
        display: none;
    }

    .book-meeting1{
        display: flex;
        width: 280px;
        margin-bottom: 10px;
        justify-content: center;
        align-items: center;
    }
  }


  .active-link {
    color: #4caf50; 
    font-weight: bold; 
  }
  