.hero-section{
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-section .left{
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 80px;
}

.hero-section .right{
    height: 100vh;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.hero-section .left h1{
    color: rgb(47, 255, 252);
    font-size: 4rem;
}

.hero-section .left p{
    color: rgb(47, 255, 252);
    font-size: 1.3rem;
    margin-bottom: 10px;
}

@media(max-width:900px){
    .hero-section{
        flex-direction: column;
        padding: 5px;
    }

    .hero-section .left{
        height: 100vh;
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 10px;
    }
    .hero-section .left h1{
        font-size: 1.9rem;
        padding: 0px;
        margin-bottom: 2px;
    }
    .hero-section .left p{
        font-size: 0.8rem;
        margin-bottom: 35px;
    }

    .hero-section .right{
        width: 100%;
    }
    
}