/* HomeScreen.css */

.home-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 20px;
    text-align: center;
    color: rgb(172, 172, 172);
  }

  .intro h1{
    margin-bottom: 10px;
  }

  .intro p{
    color: #828282;
  }
  
  .intro {
    margin-bottom: 20px;
  }
  
  .cta-button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .cta-button:hover {
    background-color: #0056b3;
  }
  
  .social-links {
    display: flex;
    justify-content: center;
  }
  
  .social-links a {
    margin: 0 10px;
    transition: transform 0.3s ease;
    color: rgb(189, 185, 185);
    font-size: 40px;
  }
  
  .social-links a:hover {
    transform: scale(1.2);
  }
  
  /* Adjust styles and add animations/effects to make it more attractive */
  

  @media(max-width:900px){
    .intro h1{
      font-size: 1rem;
      margin-bottom: 5px;
    }
  
    .intro p{
      font-size: 0.7rem;
    }

    .social-links a {
      font-size: 20px;
      margin: 0 3px;
    }

    .intro {
      margin-bottom: 1px;
    }
    
    
  }